import React from 'react';
import './aboutMe.scss';

const AboutMe = ({ observer }) => {
  const [element, setElement] = React.useState(null);

  React.useEffect(() => {
    if (element) {
      observer.observe(element);
    }
  // eslint-disable-next-line
  }, [element]);

  return (
    <section className="aboutMe" ref={setElement} data-page="about-me">
      <div className="aboutMe__left">
        <div className="aboutMe__left--bg" />
        <div className="aboutMe__left--content">
          <div className="aboutMe__left--content__shape">
            <div className="aboutMe__left--content__shape--avatar" />
            <div className="aboutMe__left--content__shape--name">Elmer Gustavo P</div>
          </div>
        </div>
      </div>
      <div className="aboutMe__right">
        <h1>Sobre M</h1>
        <p>
          Hola me llamo Gustavo, soy estudiante de la universidad Rafael Landvar
        </p>
        <p>
          Inici mi aprendizaje como programador en 2017. Tengo un especial inters en Desarrolo Web dominando mayormente Front-End, pero tambin puedo desempearme en Back-end. 
          Actualmente he creado varios proyectos como hobbie y tambin como inters en aportar soluciones a problemas de la vida cotidiana-moderna, adquiriendo conocimientos en el camino.
        </p>
        <p>
          "Nunca pares de aprender"
        </p>
        <div className="aboutMe__right--country">
          <div className="aboutMe__right--country__flag" />
          <span>Escuintla, Guatemala</span>
        </div>
      </div>
    </section>
  );
};

export default AboutMe;
